









































	export default{
		data(){
			return{
				sacName:"",
				sacPhone:"",
				sacVerification:"", //验证码
				sacPwd:"",
				Signboard:'',
				Signguid:'',
				telephone:"", //主账号的电话
				countdown:'获取验证码', //定义替换的内容
				disabled:false, //让按钮是否可用
				timestatus:false, //时间，倒计时秒数
			}
		},
		methods:{
			//获取用户输入的数据 1.用户名 2.电话 3.验证码 4.密码
			name:function(evt){
				this.sacName=evt.target.value;
			},
			phone:function(evt){
				this.sacPhone=evt.target.value;
				console.log(this.sacPhone);
			},
			verification:function(evt){
				this.sacVerification=evt.target.value;
			},
			password:function(evt){
				this.sacPwd=evt.target.value;
			},
			//点击获取验证码按钮触发的事件
			changeVerification(evt){
				this.countdown = 60;  //倒计时秒数
				this.timestatus = true; //秒数是否显示
				this.clear = setInterval(this.countDown,1000); //调用倒计时函数，每隔一秒钟调用一次
				uni.request({
					url:'http://192.168.199.194/mobile/subaccount/getCode',
					method:'POST',
					data:{
						phone:this.telephone,
						Ident_Signboard: this.Signboard,
						Ident_Signguid: this.Signguid
					},
					success: (res) => {
						if(res.data.status==0){
							uni.showToast({
								title:res.data.message,
								icon:'none'
							})
							
						}else{
							uni.showToast({
								title:'验证码发送失败',
								icon:'none'
							})
						}
					}
				})
			},
			//点击确定按钮触发的事件
			btnWarn(evt){
				if(this.sacName==""||this.sacPhone==""||this.sacVerification==""||this.sacPwd==""){
					uni.showToast({
						title:'请将信息填写完整,不允许为空',
						icon:'none'
					})
				}else if(!RegExp(/^[\u4E00-\u9FA5A-Za-z0-9]+$/).test(this.sacName)){
					uni.showToast({
						title:"用户名只能包含中文，字母，数字",
						icon: "none"
					})
				}else if(!RegExp(/^(13[0-9]|14[5|7]|15[0|1|2|3|5|6|7|8|9]|18[0|1|2|3|5|6|7|8|9]|17[0|1|2|3|5|6|7|8|9])\d{8}$/).test(this.sacPhone)){
					uni.showToast({
						title:"手机号码格式不正确",
						icon: "none"
					})
				}else if(!RegExp(/^[A-Za-z0-9]{5,16}$/).test(this.sacPwd)){
					uni.showToast({
						title:"密码只能由字母数字组成,长度不能小于6位大于16位",
						icon: "none"
					})
				}else{
					uni.request({
						url:'http://192.168.199.194/mobile/subaccount/creSubAccount',
						method:'POST',
						data:{
							Ident_Signboard: this.Signboard,
							Ident_Signguid: this.Signguid,
							loginname:this.sacName,
							phone:this.sacPhone,
							password:this.sacPwd,
							telephone:this.telephone,
							code:this.sacVerification
						},
						success: (res) => {
							if(res.data.status==0){
								uni.showToast({
									title:res.data.message,
									icon:'none'
								}),
								uni.navigateTo({
									url:'../user/subaccount'
								})
							}else{
								uni.showToast({
									title:'新建子账号失败',
									icon:'none'
								})
							}
						},
						fail: (res) => {
							uni.showToast({
								title:res.data.message,
								icon:'none'
							})
						}
					})
				}
			},
			// 倒计时
           countDown(){
                var that = this;
                if(!that.countdown){    //倒计时完后,不等于当前秒数                
                    that.disabled = false; //按钮变为可用
                    that.timestatus = false; //秒数重新倒数
                    that.countdown = '重新获取'; //按钮内容变为“重新获取”
                    clearInterval(that.clear); //清除that1
                }else{
					--that.countdown; //倒数倒计时
				}
            }
		},
		onLoad:function(){
			var jsonList=uni.getStorageSync("jsonList");
			var data = JSON.parse(jsonList); //JSON字符串转对象
			this.Signboard = data.Ident_Signboard;
			this.Signguid = data.Ident_Signguid;
			uni.request({
				url:'http://192.168.199.194/mobile/subaccount/getMain',
				method:'GET',
				data:{
					Ident_Signboard: this.Signboard,
					Ident_Signguid: this.Signguid
				},
				success: (res) => {
					console.log(res);
					if(res.data.status==0){
						this.telephone=res.data.telephone; //将获取到的主账号电话赋值给上面定义的telephone
						console.log(this.telephone);
						uni.showToast({
							title:res.data.message,
							icon:'none'
						})
					}else{
						uni.showToast({
							title:'采集信息失败',
							icon:'none'
						})
					}
				}
			})
		}
	}
